@import "https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  color: #333;
  background: linear-gradient(to right, #3a7bd5, #054a91);
  padding: 20px;
}

header {
  text-align: center;
  background: #f7f7f6f2;
  border-radius: 10px;
  padding: 15px 20px;
  box-shadow: 0 4px 6px #0000001a;
}

header h1 {
  color: #054a91;
  font-size: 28px;
  font-weight: 600;
}

.navbar {
  background: #ffffffe6;
  border-radius: 10px;
  justify-content: center;
  padding: 10px;
  display: flex;
  box-shadow: 0 4px 6px #0000001a;
}

.navbar ul {
  flex-wrap: wrap;
  list-style: none;
  display: flex;
}

.navbar li {
  margin: 0 15px;
}

.navbar a {
  color: #054a91;
  padding: 10px 15px;
  font-size: 1.1em;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s ease-in-out;
}

.navbar a:hover {
  color: #e63946;
}

main {
  background: #fff;
  border-radius: 12px;
  max-width: 900px;
  margin: 20px auto;
  padding: 25px;
  box-shadow: 0 4px 12px #0000001a;
}

#book-list, #add-book {
  text-align: center;
  background: #fff;
  border-radius: 12px;
  max-width: 600px;
  margin: 30px auto;
  padding: 25px;
  box-shadow: 0 4px 10px #0000001a;
}

#book-list h2, #add-book h2 {
  color: #054a91;
  margin-bottom: 15px;
  font-size: 22px;
  font-weight: bold;
}

#book-form input {
  border: 2px solid #ccc;
  border-radius: 8px;
  width: 100%;
  padding: 12px;
  font-size: 16px;
  transition: all .3s ease-in-out;
}

#book-form input:focus {
  border-color: #f4a261;
  outline: none;
  box-shadow: 0 4px 10px #f4a26166;
}

#book-form button {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #3a7bd5, #054a91);
  border: none;
  border-radius: 8px;
  width: 100%;
  margin-top: 10px;
  padding: 14px;
  font-size: 18px;
  font-weight: bold;
  transition: all .3s ease-in-out;
}

#book-form button:hover {
  background: linear-gradient(135deg, #054a91, #3a7bd5);
  transform: scale(1.05);
}

#books {
  padding: 0;
  list-style: none;
}

#books li {
  background: #f9f9f9;
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
  padding: 15px;
  transition: background .3s ease-in-out;
  display: flex;
  box-shadow: 0 3px 6px #00000026;
}

#books li:hover {
  background: #e3f2fd;
}

.book-title {
  color: #1d3557;
  font-size: 18px;
  font-weight: bold;
}

button {
  cursor: pointer;
  border: none;
  border-radius: 6px;
  margin-left: 8px;
  padding: 10px 14px;
  font-size: 14px;
  font-weight: bold;
  transition: all .3s ease-in-out;
}

button.edit {
  color: #fff;
  background: #2a9d8f;
}

button.edit:hover {
  background: #21867a;
}

button.delete {
  color: #fff;
  background: #e63946;
}

button.delete:hover {
  background: #c91829;
}

button.save {
  color: #fff;
  background: #f4a261;
}

button.save:hover {
  background: #e07a5f;
}

#chatbot {
  text-align: center;
  background: #fff;
  border-radius: 12px;
  margin-top: 40px;
  padding: 20px;
  box-shadow: 0 4px 12px #00000026;
}

#chatbot h2 {
  color: #054a91;
  font-size: 22px;
  font-weight: bold;
}

#chat-input {
  border: 2px solid #3a7bd5;
  border-radius: 10px;
  width: 100%;
  padding: 12px;
  font-size: 16px;
  transition: all .3s ease-in-out;
}

#chat-input:focus {
  border-color: #f4a261;
  outline: none;
  box-shadow: 0 4px 10px #f4a26166;
}

#send-btn {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #3a7bd5, #054a91);
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 16px;
  font-weight: bold;
  transition: all .3s ease-in-out;
  box-shadow: 0 4px 8px #0003;
}

#send-btn:hover {
  background: linear-gradient(135deg, #054a91, #3a7bd5);
  transform: scale(1.05);
}

#send-btn:active {
  transform: scale(.95);
}

@media (width <= 768px) {
  #books li {
    flex-direction: column;
    align-items: flex-start;
  }

  button {
    text-align: center;
    width: 100%;
  }

  #chat-input {
    padding: 10px;
    font-size: 14px;
  }

  #send-btn {
    padding: 10px 16px;
    font-size: 14px;
  }
}
